.Media {
    cursor: pointer;
    margin-right: 15px;
    display: inline-block;
    width: 260px;
    vertical-align: top;
  }
  
  .Media:hover .Media-cover {
    box-shadow: 0 0 10px rgba(0,0,0,.3);
  }
  
  .Media-cover {
    transition: .3s;
    box-shadow: 0 0 5px rgba(0,0,0,.3);
    height: 160px;
  }
  
  .Media-image {
    object-fit: cover;
  }
  
  .Media-title {
    margin: 5px 0 0 0;
    color: #44546b;
    white-space: normal;
  }
  
  .Media-author {
    font-size: 12px;
    margin: 5px 0 0 0;
  }